  font-weight: bold;
  letter-spacing: 0;
  line-height: 1.6em;
  margin-bottom: 2.6666666667vw;
  color: #006DCA;
}
@media screen and (min-width: 768px) {
  .c_flow_info_ttl {
    font-size: 1.25vw;
    margin-bottom: 0.25vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_flow_info_ttl {
    font-size: 1.25rem;
    margin-bottom: 4px;
  }
}
.c_flow.color2 {
  background-color: #F6FBF5;
}
.c_flow.color2 .c_flow_circle,
.c_flow.color2 .c_flow_circle::after {
  background-color: #3CA629;
}
.c_flow.color2 .c_flow_info_ttl {
  color: #3CA629;
}
.c_flow.color3 {
  background-color: #FBF9F5;
}
.c_flow.color3 .c_flow_circle,
.c_flow.color3 .c_flow_circle::after {
  background-color: #B78929;
}
.c_flow.color3 .c_flow_info_ttl {
  color: #B78929;
}

.c_qa {
  padding: 16vw 0 0;
}
@media screen and (min-width: 768px) {
  .c_qa {
    padding: 6vw 0 0;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa {
    padding: 96px 0 0;
  }
}
.c_qa_item {
  margin: 0 0 4vw;
}
@media screen and (min-width: 768px) {
  .c_qa_item {
    margin: 0 0 1.3125vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_item {
    margin: 0 0 21px;
  }
}
.c_qa_char {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  font-size: 7.4666666667vw;
  line-height: 1;
  margin: 0 2.6666666667vw 0 0;
}
@media screen and (min-width: 768px) {
  .c_qa_char {
    font-size: 2.1875vw;
    margin: 0 2.625vw 0 0;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_char {
    font-size: 2.1875rem;
    margin: 0 42px 0 0;
  }
}
.c_qa_heading_wrap {
  position: relative;
  margin-bottom: 1px;
  background: #F5FAFB;
  display: flex;
  padding: 4vw 9.3333333333vw 4vw 4vw;
}
@media screen and (min-width: 768px) {
  .c_qa_heading_wrap {
    padding: 2.3125vw 4.8125vw 2.3125vw 3.125vw;
    transition: all 0.5s;
  }
  .c_qa_heading_wrap:hover {
    cursor: pointer;
    opacity: 0.7;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_heading_wrap {
    padding: 37px 77px 37px 50px;
  }
}
.c_qa_heading_wrap::before, .c_qa_heading_wrap::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  background: #00ADC6;
  transition: all 0.5s;
}
.c_qa_heading_wrap::before {
  height: 1px;
  width: 2.6666666667vw;
  right: 4.2666666667vw;
}
@media screen and (min-width: 768px) {
  .c_qa_heading_wrap::before {
    width: 1vw;
    right: 3.25vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_heading_wrap::before {
    width: 16px;
    right: 52px;
  }
}
.c_qa_heading_wrap::after {
  right: 8px;
  height: 2.6666666667vw;
  width: 1px;
  right: 5.3333333333vw;
}
@media screen and (min-width: 768px) {
  .c_qa_heading_wrap::after {
    height: 1vw;
    right: 3.75vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_heading_wrap::after {
    height: 16px;
    right: 60px;
  }
}
.c_qa_heading_wrap.is_open::after {
  transform: translateY(-50%) rotate(90deg);
}
.c_qa_heading_q {
  color: #2C68C9;
}
.c_qa_heading .head {
  font-weight: bold;
  letter-spacing: 0;
  font-size: 4.8vw;
  line-height: 1.5em;
}
@media screen and (min-width: 768px) {
  .c_qa_heading .head {
    font-size: 1.5625vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_heading .head {
    font-size: 1.5625rem;
  }
}
.c_qa_main {
  display: none;
}
.c_qa_main_wrap {
  background: #F5FAFB;
  display: flex;
  padding: 4vw;
}
@media screen and (min-width: 768px) {
  .c_qa_main_wrap {
    padding: 1.3125vw 3.75vw 1.875vw 3.3125vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_main_wrap {
    padding: 21px 60px 30px 53px;
  }
}
.c_qa_main_a {
  color: #9BA6AF;
}
@media screen and (min-width: 768px) {
  .c_qa_main_a {
    top: 0.125vw;
  }
}
@media screen and (min-width: 1601px) {
  .c_qa_main_a {
    top: 2px;
  }
}
.c_qa_list.color2 .c_qa_heading_wrap {
  background-color: #F6FBF5;
}
.c_qa_list.color2 .c_qa_heading_wrap::after, .c_qa_list.color2 .c_qa_heading_wrap::before {
  background-color: #3CA629;
}
.c_qa_list.color2 .c_qa_char {
  color: #3CA629;
}
.c_qa_list.color3 .c_qa_heading_wrap {
  background-color: #FBF9F5;
}
.c_qa_list.color3 .c_qa_heading_wrap::after, .c_qa_list.color3 .c_qa_heading_wrap::before {
  background-color: #B78929;
}
.c_qa_list.color3 .c_qa_char {
  color: #B78929;
}