@use "../../global"as g;

/*!
page > common_low
------------------------------
*/
.container {
    &_1225 {
        width: 100%;


        @include g.mq('md') {
            width: g.vw(1225);
            margin-left: auto;
            margin-right: auto;
        }
    }
}

.mv_low {
    position: relative;
    margin-bottom: g.vw(25, 375);
    padding-top: g.vw(62, 375);

    @include g.mq('md') {
        height: g.vw(575);
        padding-top: g.vw(150);
        margin-bottom: g.vw(43);
    }

    @include g.mq('xxl') {
        height: 575px;
        padding-top: 150px;
        margin-bottom: 43px;
    }

    &::before {
        @include g.mq('md') {
            position: absolute;
            content: '';
            top: calc(100% - g.vw(81));
            left: 0;
            background: url(../images/common_low/img_mv_shape.png) no-repeat center/cover;
            width: 100%;
            height: g.vw(81);
        }
    }

    &_bg {
        position: relative;
        width: 100%;
        height: g.vw(134.6, 375);
        top: 0;
        left: 0;
        background: #F5FAFB;
        z-index: -1;
        margin-bottom: g.vw(30, 375);

        &::before {
            position: absolute;
            content: '';
            top: calc(100% - g.vw(18.87, 375));
            left: 0;
            background: url(../images/common_low/img_mv_shape.png) no-repeat center/cover;
            width: 100%;
            height: g.vw(18.87, 375);
        }

        @include g.mq('md') {
            margin-bottom: 0;
            height: 100%;
            position: absolute;

            &::before {
                content: none;
            }
        }
    }

    &_container {
        display: flex;
        align-items: center;
        padding-left: min((20/320)*100vw, 30px);
        padding-right: min((20/320)*100vw, 30px);
        max-width: 100%;
        width: 100%;
        margin: 0 auto;
        height: 100%;

        @include g.mq('md') {
            align-items: inherit;
            padding: g.vw(154) g.vw(20) 0;
            width: g.$width-content;
        }

        @include g.mq('xxl') {
            padding: 154px 20px 0;
            width: 1200px;
        }
    }

    &_ttl {
        display: block;

        .ttl_en {
            @include g.fonts(g.$f_mosarat, 30, 400, 30, 30, #00ADC6);
            font-size: g.vw(25, 375);
            display: block;
            color: #00ADC6;

            @include g.mq('md') {
                font-size: g.vw(30);
            }

            @include g.mq('xxl') {
                font-size: g.rem(30);
            }
        }

        .ttl_jp {
            display: block;
            @include g.fonts(0, 30, bold, 0, 0, #44474E);
            line-height: 1.6em;
            font-size: g.vw(30, 375);

            @include g.mq('md') {
                font-size: g.vw(40);
            }

            @include g.mq('xxl') {
                font-size: g.rem(40);
            }
        }
    }
}

.breadcrumb {
    padding: 0 min((20/320)*100vw, 30px);

    @include g.mq('md') {
        padding: 0;
    }

    &_links {
        background: #fff;
        margin: 0 auto;
        padding: 0 0 g.vw(25, 375);
        border-bottom: solid 1px #E6EBEF;

        @include g.mq('md') {
            max-width: g.vw(1160);
            padding: 0 0 g.vw(22);
        }

        @include g.mq('xxl') {
            max-width: 1160px;
            padding: 0 0 22px;
        }
    }

    &_link {
        position: relative;
        display: inline;
        line-height: 1.5em;
        color: #00ADC6;
        letter-spacing: 0;

        @include g.mq('md') {
            font-size: g.vw(14);
        }

        @include g.mq('xxl') {
            font-size: g.rem(14);
        }

        &:not(:last-child) {
            padding: 0 g.vw(20, 375) 0 0;
            margin: 0 g.vw(16, 375) g.vw(10, 375) 0;

            @include g.mq('md') {
                padding: 0 g.vw(23) 0 0;
                margin: 0 g.vw(16) g.vw(10) 0;
            }

            @include g.mq('xxl') {
                padding: 0 23px 0 0;
                margin: 0 16px 10px 0;
            }

            &::after {
                content: '';
                border-top: solid g.vw(4, 375) transparent;
                border-left: solid g.vw(4, 375) #9BA6AF;
                border-bottom: solid g.vw(4, 375) transparent;
                position: absolute;
                top: g.vw(7, 375);
                right: 0;

                @include g.mq('md') {
                    top: g.vw(6);
                    border-top: solid g.clamped(2, 4, 768, 1600) transparent;
                    border-left: solid g.clamped(2, 4, 768, 1600) #9BA6AF;
                    border-bottom: solid g.clamped(2, 4, 768, 1600) transparent;
                }

                @include g.mq('xxl') {
                    top: 6px;
                    border-top: solid 4px transparent;
                    border-left: solid 4px #9BA6AF;
                    border-bottom: solid 4px transparent;
                }
            }
        }

        .link {
            color: #9BA6AF;
            text-decoration: none;
            position: relative;

            @include g.mq('md') {
                transition: all 0.5s;

                &:hover {
                    opacity: 0.7;
                }
            }

            &::after {
                position: absolute;
                content: '';
                bottom: g.vw(1);
                left: 0;
                background: #9BA6AF;
                height: 1px;
                width: 100%;

                @include g.mq('xxl') {
                    bottom: 1px;
                }
            }
        }

    }
}

