.c_list_number {
    counter-reset: list-num;

    .item {
        position: relative;
        display: inline-block;
        padding-left: g.vw(25, 375);
        margin-bottom: g.vw(20, 375);

        @include g.mq('md') {
            padding-left: g.vw(33);
            margin-bottom: g.vw(42);
        }

        @include g.mq('xxl') {
            padding-left: 33px;
            margin-bottom: 42px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        &::before {
            position: absolute;
            left: 0;
            top: 0;
            background: none;
            border: 0;
            counter-increment: list-num;
            content: counter(list-num) '.';
            @include g.fonts(g.$f_mosarat, 25, bold, 45, 0, #00ADC6);
            font-size: g.vw(22, 375);

            @include g.mq('md') {
                font-size: g.vw(25);
            }

            @include g.mq('xxl') {
                font-size: g.rem(25);
            }
        }
    }

    .ttl {
        display: block;
        @include g.fonts(0, 25, bold, 45, 0, #44474E);
        font-size: g.vw(22, 375);
        margin-bottom: g.vw(5, 375);

        @include g.mq('md') {
            font-size: g.vw(25);
            margin-bottom: g.vw(15);
        }

        @include g.mq('xxl') {
            font-size: g.rem(25);
            margin-bottom: 15px;
        }
    }
}

.c_low_tbl {
