        &:last-child {
            border-bottom: 0;
        }

        @include g.mq('md') {
            border-bottom: 0;
            flex: 1;

            &:not(:last-child) {

                .c_low_tbl_head,
                .c_low_tbl_content {
                    border-right: solid 1px #E6EBEF;
                }
            }

            &:last-child {
