            }
        }
    }

    &_head {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: g.vw(10, 375);
        background: #E6F0F7;
        font-weight: bold;
        color: #44474E;
        letter-spacing: 0;
        border-bottom: solid 1px #E6EBEF;

        @include g.mq('md') {
            border-bottom: 0;
            padding: g.vw(13) g.vw(14) g.vw(15);
        }

        @include g.mq('xxl') {
            padding: 13px 14px 15px;
        }
    }

    &_content {
        padding: g.vw(10, 375);
        text-align: center;

        &.bg01 {
            background: #F5FAFB;
        }

        .desc {
            font-weight: bold;
            color: #44474E;
            letter-spacing: 0;
        }

        @include g.mq('md') {
            padding: g.vw(13) g.vw(14) g.vw(14);
        }

        @include g.mq('xxl') {
