        }
    }
}

/*Pagination*/
.pagination {
    &_list {
        display: flex;
        justify-content: center;
    }

    &_item {
        width: g.vw(40, 375);
        height: g.vw(40, 375);
        margin: g.vw(3, 375);

        @include g.mq('md') {
            width: g.vw(49);
            height: g.vw(49);
            margin: g.vw(10) g.vw(10) 0;
        }

        @include g.mq('xxl') {
            width: 49px;
            height: 49px;
            margin: 10px 10px 0;
        }

        a {
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            text-decoration: none;
            background: #fff;
            color: #9BA6AF;
            border: solid 1px #E6EBEF;
            width: 100%;
            height: 100%;
            font-family: 'Montserrat', sans-serif;
            font-size: g.rem(16);
            font-weight: 600;
            letter-spacing: 0.03em;
            line-height: 1.5em;

            @include g.mq('sm') {
                font-size: g.vw(16, 376);
            }

            @include g.mq('md') {
                font-size: g.vw(18);
                transition: all 0.5s;

                &:hover {
                    opacity: 0.8;
                    background: #00ADC6;
                    color: #fff;
                    border: solid 1px #00ADC6;
                }
            }

            @include g.mq('xxl') {
                font-size: g.rem(18);
            }
        }

        &_next,
        &_prev {
            margin: g.vw(5, 375) g.vw(10, 375) 0;

            @include g.mq('md') {
                margin: g.vw(10) g.vw(20) 0;
            }

            @include g.mq('xxl') {
                margin: 10px 20px 0;
            }

            a {
                background: #1D4156;
                border: solid 1px #1D4156;
            }

            img {
                width: g.vw(10, 375);
                height: g.vw(10, 375);

                @include g.mq('md') {
                    width: g.vw(12);
                    height: g.vw(12);
                }

                @include g.mq('xxl') {
                    width: 12px;
                    height: 12px;
                }
            }
        }

        &_prev {
            img {
                transform: scaleX(-1);
            }
        }

        &.disabled {
            a {
                background: #E6EBEF;
                border: solid 1px #E6EBEF;
                pointer-events: none;
                color: #fff;
            }
        }

        &.active {
            a {
                background: #00ADC6;
                color: #fff;
                border: solid 1px #00ADC6;
            }
        }
    }
}

.wp-pagenavi {
    display: flex;
    justify-content: center;

    .pages {
        display: none;
    }

    .page,
    .current {
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        text-decoration: none;
        background: #fff;
        color: #9BA6AF;
        border: solid 1px #E6EBEF !important;
        font-family: 'Montserrat', sans-serif;
        font-size: g.rem(16);
        font-weight: 600;
        letter-spacing: 0.03em;
        line-height: 1.5em;
        width: g.vw(40, 375);
        height: g.vw(40, 375);
        padding-top: g.vw(3, 320);
        margin: g.vw(3, 375);

        @include g.mq('sm') {
            font-size: g.vw(16, 376);
            padding-top: 0;
        }

        @include g.mq('md') {
            width: g.vw(49);
            height: g.vw(49);
            margin: g.vw(10) g.vw(10) 0;
            font-size: g.vw(18);
            transition: all 0.5s;

            &:hover {
                background: #00ADC6;
                color: #fff;
                border: solid 1px #00ADC6 !important;
            }
        }

        @include g.mq('xxl') {
            width: 49px;
            height: 49px;
            margin: 10px 10px 0;
            font-size: g.rem(18);
        }
    }

    .current {
        background: #00ADC6;
        color: #fff;
        border: solid 1px #00ADC6 !important;
    }

    .previouspostslink,
    .nextpostslink {
        margin: g.vw(5, 375) g.vw(10, 375) 0;
        border: solid 1px #1D4156 !important;
        border-radius: 100%;
        width: g.vw(40, 375);
        height: g.vw(40, 375);
        color: transparent;

        @include g.mq('md') {
            width: g.vw(49);
            height: g.vw(49);
            margin: g.vw(10) g.vw(20) 0;
            transition: all 0.5s;

            &:hover {
                background: #00ADC6 url(../images/news/icon_arrow02.svg) no-repeat center;
                background-size: 12px;
                color: transparent;
                border: solid 1px #00ADC6 !important;
            }
        }

        @include g.mq('xxl') {
            width: 49px;
            height: 49px;
            margin: 10px 20px 0;
        }

        img {
            width: g.vw(10, 375);
            height: g.vw(10, 375);

            @include g.mq('md') {
                width: g.vw(12);
                height: g.vw(12);
            }

            @include g.mq('xxl') {
                width: 12px;
                height: 12px;
            }
        }

        &.disable {
            background: #E6EBEF url(../images/news/icon_arrow02.svg) no-repeat center;
            background-size: 12px;
            border: solid 1px #E6EBEF !important;
            pointer-events: none;
            color: transparent;
        }
    }

    .previouspostslink {
        background: #1D4156 url(../images/news/icon_arrow02.svg) no-repeat center;
        background-size: 12px;
        transform: scaleX(-1);
    }

    .nextpostslink {
        background: #1D4156 url(../images/news/icon_arrow02.svg) no-repeat center;
        background-size: 12px;
    }
}

