/**********************************************************/
.mv_low_gradient {
    img {
        mix-blend-mode: multiply;
        -webkit-mix-blend-mode: multiply;
    }

    &::before {
        top: unset;
        bottom: -2px;

        @include g.mq('md') {
            top: unset;
        }
    }

    .mv_low_bg {
        background: rgb(102, 172, 192);
